import React from 'react'
import { Link } from 'react-router-dom'
import useUser from '../hook/useUser'
import { useNavigate } from 'react-router-dom'
import Cookies from 'js-cookie'

function TheHeader() {
    const navigate = useNavigate()
    const isUser = useUser()
    return (
        <header>
            <div className="navbar navbar-expand-xl container-xl">
                <div className="container-fluid">
                    <a className="logo navbar-brand fs-2" href="/">AxTaxy</a>
                    <button className="navbar-toggler" data-bs-toggle="collapse" data-bs-target="#menu">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <nav className="collapse navbar-collapse menu" id="menu">
                        <ul className="navbar-nav d-none d-xl-flex">
                            <li className="mx-2">
                                <Link to="/" className="p-1 d-xl-block d-none">
                                    首頁
                                </Link>
                            </li>
                            <li className="mx-2">
                                <Link to="/about" className="p-1 d-xl-block d-none">
                                    關於Axtaxy                 
                                </Link>
                            </li>
                            {
                                isUser && (
                                    <li className="mx-2">
                                        <Link to="/system"  className="p-1 d-block" >
                                            建立新任務
                                        </Link>
                                    </li>
                                )                          
                            }
                            {
                                isUser ? 
                                    JSON.parse(isUser)?.level === 1 && (
                                        <li className="mx-2">
                                            <Link to="/labsystem"  className="p-1 d-block">
                                                系統總覽
                                            </Link>
                                        </li>
                                    ) : 
                                    null
                            }
                            {
                                !isUser ? 
                                    <li className="mx-2">
                                        <Link to="/login"  className="p-1 d-block">
                                            登入
                                        </Link>
                                    </li>
                                    :
                                    <li className="mx-2" onClick={() => {
                                        Cookies.remove('user')
                                        navigate('/')
                                    }}>
                                        <span to="/"  className="p-1 d-block ">
                                            登出
                                        </span>
                                    </li>
                            }
                            
                        </ul>
                        {/*  */}
                        <ul className="navbar-nav d-xl-none">
                            <li className="mx-2" data-bs-toggle="collapse" data-bs-target="#menu">
                                <Link to="/" className="p-1 d-block d-xl-none">
                                    首頁
                                </Link>
                            </li>
                            <li className="mx-2" data-bs-toggle="collapse" data-bs-target="#menu">
                                <Link to="/about" className="p-1 d-block d-xl-none">
                                    關於Axtaxy
                                </Link>
                            </li>
                            {
                                isUser && (
                                    <li className="mx-2">
                                        <Link to="/system" className="p-1 d-block"  data-bs-toggle="collapse" data-bs-target="#menu">
                                            建立新任務
                                        </Link>
                                    </li>
                                )                          
                            }
                            {
                                isUser ? 
                                    JSON.parse(isUser)?.level === 1 && (
                                        <li className="mx-2">
                                            <Link to="/labsystem" className="p-1 d-block" data-bs-toggle="collapse" data-bs-target="#menu">
                                                系統總覽
                                            </Link>
                                        </li>
                                    ) : 
                                    null
                            }
                            {
                                !isUser ? 
                                    <li className="mx-2">
                                        <Link to="/login"  className="p-1 d-block">
                                            登入
                                        </Link>
                                    </li>
                                    :
                                    <li className="mx-2" onClick={() => {
                                        Cookies.remove('user')
                                        navigate('/')
                                    }}>
                                        <span to="/"  className="p-1 d-block ">
                                            登出
                                        </span>
                                    </li>
                            }
                            
                        </ul>
                    </nav>
                </div>
            </div>
        </header>
    )
}

export default TheHeader